.theBox{
    margin: 7vh auto;
    padding: 2vh;
    background-color: white;
    border-radius: 25px;
    min-height: 30vh;
    box-shadow: 0px 15px 25px -6px rgba(196,196,196,1);
}

.theBox h2{
    color:#222222;
    font-size: 3em;
    font-family: 'Raleway', sans-serif;
}

.theBox b{
    font-size: 1.2em;
}

.download{
    font-size: 1.7em;
    letter-spacing: 3px;
}

.research{
    margin: 0 5vw;
}
.research h4{
    margin: 3vh auto;
    color:#c39d63;
    font-size: 1.7em;
}

.research p{
    line-height: 2em;
    font-size: 1.2em;
}